@font-face {
  font-family: "Gilroy";
  src: url("/fonts/Gilroy-Regular.woff2") format("woff2"), url("/fonts/Gilroy-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Nekst";
  src: url("/fonts/Nekst-Black.woff2") format("woff2"), url("/fonts/Nekst-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 46px;
}
@media (max-width: 768px) {
  .container {
    padding: 0 36px;
  }
}
@media (max-width: 635px) {
  .container {
    padding: 0 20px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 768px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 768px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 768px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.calculator {
  background-color: #ffffff;
  padding-top: 220px;
  padding-bottom: 50px;
}

.calculator__title {
  margin: 0;
  font-family: "Nekst", "Arial", sans-serif;
  font-size: 54px;
  line-height: 90%;
}

.calculator__form {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 32px;
  row-gap: 51px;
  margin-top: 26px;
}

.calculator__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.calculator__item-title {
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: start;
  width: 100%;
}

.calculator__item-title-mobile {
  display: none;
}

.calculator__range-result {
  position: relative;
  min-height: 68px;
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Nekst", "Arial", sans-serif;
  font-size: 30px;
  line-height: 36px;
  border-radius: 16px;
  color: #575757;
  padding: 12px 24px 5px;
  background-color: #F3F3F4;
  margin-top: 24px;
  width: 100%;
  cursor: text;
}
.calculator__range-result--contribution {
  justify-content: start;
}
.calculator__range-result:focus-within {
  background-color: #ffffff;
  border: 1px solid #F3F3F4;
}

.calculator__range-result--price:hover ~ .calculator__range--price {
  height: 1px;
}

.calculator__range-result--contribution:hover ~ .calculator__range--contribution {
  height: 1px;
}

.calculator__range-result--term:hover ~ .calculator__range--term {
  height: 1px;
}

.calculator__range-result-number {
  margin-right: 7px;
  border: none;
  background-color: rgba(255, 255, 255, 0);
  color: #575757;
}
.calculator__range-result-number--hidden {
  display: none;
}
@media (hover: hover) {
  .calculator__range-result-number:hover:not(.focus-visible) {
    outline: none;
  }
}
.calculator__range-result-number.focus-visible:focus {
  outline: none;
}

.calculator__range-result-number--price:focus ~ .calculator__range--price {
  height: 1px;
}

.calculator__range-result-number--contribution:focus ~ .calculator__range--contribution {
  height: 1px;
}

.calculator__range-result-number--term:focus ~ .calculator__range--term {
  height: 1px;
}

.calculator__range-result-number--price:disabled ~ .calculator__range-result--price {
  opacity: 0.5;
}

.calculator__range-result-number--contribution:disabled ~ .calculator__range-result--contribution {
  opacity: 0.5;
}

.calculator__range-result-number--term:disabled ~ .calculator__range-result--term {
  opacity: 0.5;
}

.calculator__range-result-text--rouble {
  padding-bottom: 5px;
}
.calculator__range-result-text--contribution {
  margin-left: 7px;
}

.calculator__range-result-placeholder {
  border: none;
  color: #575757;
  cursor: text;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: start;
  align-items: center;
  background-color: rgba(255, 255, 255, 0);
}
.calculator__range-result-placeholder--hidden {
  display: none;
}
.calculator__range-result-placeholder--contribution {
  width: auto;
}

.calculator__range {
  -webkit-appearance: none;
  width: 89%;
  height: 2px;
  background: #e1e1e1;
  background-image: linear-gradient(#FF9514, #FF9514);
  background-repeat: no-repeat;
  z-index: 2;
  margin-top: -2px;
}
.calculator__range--price {
  background-size: 80% 100%;
}
.calculator__range--contribution {
  background-size: 35% 100%;
}
.calculator__range--term {
  background-size: 80% 100%;
}
@media (hover: hover) {
  .calculator__range:hover:not(.focus-visible) {
    height: 1px;
  }
}
.calculator__range.focus-visible:focus {
  height: 1px;
}

.calculator__range::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #FF9514;
  cursor: ew-resize;
  transition: background 0.3s ease-in-out;
}

.calculator__range::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  box-shadow: none;
  border: none;
  background: transparent;
}

.calculator__number-percent {
  position: absolute;
  right: 8px;
  top: 7px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 69px;
  min-height: 54px;
  background-color: #EBEBEC;
  border-radius: 16px;
}

.calculator__number-percent-text {
  font-size: 20px;
  line-height: 120%;
  padding-top: 5px;
}

.calculator__result {
  display: flex;
  font-family: "Nekst", "Arial", sans-serif;
  font-size: 54px;
  line-height: 90%;
}

.calculator__result-title {
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 16px;
  line-height: 150%;
  color: #575757;
}

.calculator__result-number {
  margin-top: 15px;
  margin-right: 14px;
}

.calculator__result-text {
  margin-top: 8px;
}

.calculator__button-submit {
  margin: 9px 0;
  border: none;
  background-color: #FF9514;
  color: #ffffff;
  font-family: "Nekst", "Arial", sans-serif;
  font-size: 30px;
  line-height: 36px;
  border-radius: 40px;
  cursor: pointer;
}
.calculator__button-submit:disabled {
  opacity: 0.4;
}
@media (hover: hover) {
  .calculator__button-submit:hover:not(.focus-visible) {
    background-color: #111111;
  }
}
.calculator__button-submit.focus-visible:active {
  background-color: #575757;
}

@media (max-width: 1120px) {
  .calculator__range-result-number--price {
    margin-left: 10px;
  }
  .calculator__range-result-number--term {
    margin-left: 7px;
  }
  .calculator__result {
    display: flex;
    flex-wrap: wrap;
  }
  .calculator__range-result-placeholder--price {
    margin-left: 9px;
  }
  .calculator__range-result-placeholder--term {
    margin-left: 5px;
  }
}
@media (max-width: 989px) {
  .calculator__result {
    font-size: 39px;
  }
  .calculator__range-result {
    font-size: 20px;
  }
  .calculator__button-submit {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .calculator {
    padding-top: 71px;
  }
  .calculator__form {
    margin-top: 39px;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 39px;
    column-gap: 8px;
  }
  .calculator__item {
    grid-column: 1/-1;
  }
  .calculator__range-result-number--price {
    margin-left: 0;
  }
  .calculator__range-result-number--term {
    margin-left: 0;
  }
  .calculator__range {
    width: 93%;
  }
  .calculator__result-wrapper {
    margin-top: 3px;
  }
  .calculator__result-wrapper--two {
    margin-left: 12px;
  }
  .calculator__button-submit {
    margin-top: 0;
    grid-column: 1/-1;
    width: 345px;
    min-height: 68px;
    font-size: 30px;
  }
  .calculator__result {
    font-size: 54px;
  }
  .calculator__range-result {
    font-size: 30px;
  }
  .calculator__range-result-placeholder--price {
    margin-left: 0;
  }
  .calculator__range-result-placeholder--term {
    margin-left: 0;
  }
}
@media (max-width: 635px) {
  .calculator {
    padding-top: 48px;
  }
  .calculator__form {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    row-gap: 0;
  }
  .calculator__button-submit {
    width: 100%;
    margin-top: 30px;
    font-size: 22px;
    min-height: 60px;
  }
  .calculator__title {
    font-size: 34px;
  }
  .calculator__item:not(:first-child) {
    margin-top: 26px;
  }
  .calculator__item-title {
    font-size: 14px;
    line-height: 136%;
  }
  .calculator__item-title-desktop {
    display: none;
  }
  .calculator__item-title-mobile {
    display: block;
  }
  .calculator__range-result {
    margin-top: 7px;
    font-size: 22px;
    padding: 10px 20px 3px;
    min-height: 60px;
  }
  .calculator__range {
    width: 87%;
  }
  .calculator__number-percent-text {
    font-size: 22px;
    color: #333E48;
  }
  .calculator__number-percent {
    width: 67px;
    height: 48px;
    right: 5px;
    top: 6px;
  }
  .calculator__result-wrapper {
    margin-top: 23px;
  }
  .calculator__result-wrapper--two {
    margin-left: 0;
  }
  .calculator__result-title {
    font-size: 14px;
    line-height: 136%;
  }
  .calculator__result {
    font-size: 22px;
  }
  .calculator__result-number {
    margin-right: 6px;
    margin-top: 3px;
  }
  .calculator__result-text {
    margin-top: 0;
  }
}
